<template>
    <div>
        <div id="login">
       <my-header></my-header>
        <div class="login_img">
        </div>
        <div class="login_content">   
             <div class="tel tels">
               <router-link :to="{name:'selectzone'}">
              <div class="selecttel">+{{tel[0]}}</div>   
               </router-link>
               <input type="number" name="userId" placeholder="" value="" v-model="loginForm.username" id="username" @blur="getcell()">
             </div>
             <div class="invite_get">
               <input type="password" name="userId" placeholder="" value="" @focus="onfocus()" id="password" v-model="loginForm.password">
             </div>
             <!---->
            <div class="proving">
               <huakuai-yan v-if="isshow[0]"></huakuai-yan>   
              </div>
            <div class="dealpsw">
              <div class="dealpsw_l"> <input type="checkbox" @click="rememberpsws()"> <span></span> </div>
               <router-link :to="{name:'forget'}"><div class="dealpsw_r"> ? </div></router-link>
              </div> 
           <div class="submit" @click="login()">
                      <input type="button" name="btnSubmit" value="" id="submit">   
           </div>
          <div class="login_foot">
              ?<router-link :to="{name:'register'}"><span></span></router-link>
          </div> 
  
        </div>
         </div>
    </div>
</template>
<script>
import myHeader from "../../frame/MyHeader.vue";
import huakuaiYan from "./huankuaiyanzheng.vue";
export default {
  components: {
    myHeader,
    huakuaiYan
  },
  methods: {
    check() {}
  },
  data(){
    return {
      loginForm:{
        username:"",
        password:"",
      },
      tel:["86"],
      cellphoto:[],
       isshow:[false],
      rememberpsw:false,  
    }
  },
  methods:{
    /**  */
           login() {
                this.$http.post(this.$api.login, {cellphone:this.loginForm.username,password:this.loginForm.password}).then(rsp => {
                      /**cookie */
                      console.log(rsp);
                      console.log(rsp.header);
                                          
                      let token = document.cookie;
                        this.$api.token=token;
                          // this.$set(this.$api.isLogin,0,true)
                          // console.log(this.$api.isLogin);
                        // this.$router.push({path:'/home'})
                            if(rsp.data.msg!=""){
                              this.loginForm.password=""
                              alert(rsp.data.msg)
                            } else{ alert("")  }    
                })
         },
         /** */
                 getcell(){
                   if(this.loginForm.username==''){
                    alert("")
                   }
                     this.$api.cellphone=this.loginForm.username;                          
                 },
       //
         rememberpsws(){
           this.rememberpsw=!this.rememberpsw;
           console.log( this.rememberpsw);    
              if(this.rememberpsw==true){
                 localStorage.setItem("usermane",this.loginForm.username);
                 localStorage.setItem("userpsw",this.loginForm.password);
              }
         },
         /** */
         onfocus(){ 
     if(this.loginForm.username!=''){ this.$set(this.isshow,0,true)}                     
         } ,
  },
  created(){

    this.$set(this.tel,this.tel,this.$api.telphoto);
  },
};
</script>
<style scoped>
#login{
  width: 100%;
  margin: 0 auto;
  font-size: 0.24rem;
}
.login_img {
  width: 100%;
  height: 4rem;
  background: url("../../../img/register.png") no-repeat;
  background-size: 100% 100%;
}
.login_content{
  width: 100%;
}
#loginform{
  padding-left: 0.28rem;
  padding-right: 0.25rem;
}
.tel {
  width: 100%;
  height: 0.8rem;
  margin:0 auto;
  border-bottom: 1px solid #edeef3;
  background: url("../../../img/shouji.png") no-repeat;
  background-size: 0.24rem 0.43rem;
  background-position-y: 0.2rem;
    background-position-x: 0.26rem;
  line-height: 0.8rem;
  text-align: center;
}
.tels{
  position: relative;
}
#username{
  width: 80%;
  height: 0.8rem;
  top: 0;
  left: 0.4rem;
  position: absolute;
  font-size: 0.24rem;
}
.selecttel{
  width: 0.7rem;
  height: 0.8rem;
  position: absolute;
  top: 0;
  left: 0.6rem;
  color: black;
  font-size: 0.2rem;
  background: url("../../../img/teldown.png") no-repeat;
  background-size: 0.12rem 0.1rem;
  background-position: 0.59rem 0.36rem;
  text-align: center;
}
.tel input {
  width: 80%;
  height: 0.8rem;
  outline: none;
  border: none;
  margin-left: 1rem;
}
.invite_get {
  width: 100%;
  height: 0.8rem;
  margin: auto;
  border-bottom: 1px solid #edeef3;
   background: url("../../../img/mima.png") no-repeat; 
  background-size:0.30rem 0.36rem;
  background-position-y: 0.22rem;
  background-position-x: 0.22rem;
  line-height: 0.8rem;
  text-align: center;
}
.invite_get input {
  width: 80%;
  height: 0.8rem;
  outline: none;
  border: none;
  margin-left: 0.2rem;
}

.proving{
  width: 100%;
}
iframe{
  width: 100%;
  height: 52px;
  font-size: 12px;
  margin-top: 0.1rem;
}
 #submit{
   width: 6.92rem;
   height: 0.7rem;
   list-style: none;
 background-color: #e03434;
   color: white;
   outline: none;
   border: none;
     text-align: center;
   border-radius: 0.325rem;
   line-height: 0.7rem;
 }   
.login_foot{
    margin-top: 0.2rem;
    font-size: 0.22rem;
    color: #c5c5c5;
}
.login_foot span{
    color: #e03434;
}
/***/
.dealpsw{
  height: 0.4rem;
  font-size: 0.2rem;
  line-height: 0.4rem;
  display: flex;
  justify-content: space-around;
  padding: 0.2rem 0.4rem;
}
.dealpsw_l input{
  vertical-align:middle
}
.dealpsw_r{
  color: #c5c5c5;
}
</style>



