<template>
    <div>
       <div class="haixuan_content">
         <div id="haixuan" >
            <div class="haixuans" :class="haixuan_bgc[0]?'haixuan_bgc':'mousehover'" @click="haixuan_click(0)"></div>
            <div class="haixuans" :class="haixuan_bgc[1]?'haixuan_bgc':'mousehover'" @click="haixuan_click(1)"></div>
            <div class="haixuans" :class="haixuan_bgc[2]?'haixuan_bgc':'mousehover'" @click="haixuan_click(2)"></div>
        </div>          
       </div>

        <div class="haixuan_list">
            <!---->
            <div class="list" :class="getpaiming.listheight?'list_height':'list'" v-for="(items,indexs) in projectlist">
                  <div class="list_top">
                    <div class="list_t_l">
                 
                    <img src="../../../../img/guoqi.png" alt="">&nbsp
                     
                    <span>/</span>&nbsp
                 
                    <span>2018/09/10-2018-12-3</span>
                    <div class="toupiao">
                     <img src="../../../../img/toupiao.png" alt="">&nbsp
                     <span></span>
              
                    </div>
                   </div>
                  </div>
                  <div class="list_center">
                      <div class="list_c_l">
                          <div class="list_c_l_img">
                          </div>
                      </div>
                      <div class="list_c_r">
                          <div v-for="item in name" class="list_c_r_name">
                              {{item}}:
                          </div>
                      </div>
                  </div>
                  <div class="list_c_b">
                      <div class="list_c_b_huo">
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                      </div>
                      <div class="list_c_b_p">
                          50123456
                      </div>
                      <div class="list_c_b_paiming" v-on:click.stop="getpaimings(indexs)">
                          
                         <img src="../../../../img/xia.png" alt="">                            
                      </div>
                  </div>
                    
               <!---->
               
        <div  class="project_name"  v-show="projectlist[indexs]">
            <div class="project_name_list" v-for="(item,index) in projectnamelist">
                <div class="project_name_list_l">
                    <img src="../../../../img/projectnameimg.png" alt="">
                </div>
                <div class="project_name_lis_c">
                    <div class="project_name_lis_c_top">
                        <span>1</span>
                        <span>123456</span>
                    </div>
                   <div class="project_name_lis_c_b">
                       <span> : 30</span>
                       <span> : 50</span>
                       <span> : </span>
                    </div>                    
                </div>
                <div class="project_name_lis_r"  v-on:click.stop="toupiao(index,indexs)" :class="toupiaostyle[index]?'project_name_lis_r_at':''">
                    {{toupiaomsg[index]}}
                </div>
            </div>
        </div>                     
       </div>   
        </div>
    </div>
</template>
<script>
  export default {
        methods:{
             toupiao(index,indexs){ 
              if(this.toupiaomsg[index]==""){this.$api.voted[0]++}                                                     
            this.$set(this.toupiaomsg,index,"") ; 
             this.$set(this.toupiaostyle,index,true) ;
             
             console.log(this.$api.voted);                                         
             },
             getpaimings(indexs){                 
                this.getpaiming.show=this.getpaiming.show?false:true;
                 this.getpaiming.listheight= !this.getpaiming.listheight;         
                      this.projectlist[indexs]=!this.projectlist[indexs] ;                                                                             
             },
             haixuan_click(index){
                 this.haixuan_bgc[0]=false
                 this.haixuan_bgc[1]=false
                 this.haixuan_bgc[2]=false
                 this.$set(this.haixuan_bgc,index,true)                                            
             },
             //
             getData(){           
              this.$http.post(this.$api.getGameRoad).then(rsp=>{});
              //  ;1-2-3 ; ;
              this.$http.post(this.$api.getGame,{process_name:"123",event_type:1,page_no:1,page_size:1}).then(rsp=>{});
              let ssss=document.cookie
              console.log(ssss);
              
             }
        },

       data(){
           return {
               haixuan_bgc:[true,false,false],
               projectnamelist:["1","1","1"],
                     name:["","","","",""],
                     toupiaomsg:["","",""],
                     toupiaoed:"",
                     projectlist:[true,false],
                     nowIndex:0,
                     toupiaostyle:[false,false,false,false],
                     getpaiming:{
                         listheight:false,
                         show:false,
                     }, 
                     getsaicheng:''                   
           }
       },
       created(){
         this.getData();
         console.log(this.$api.isLogin);
         
       },
    }
</script>
<style scoped>
.haixuan_content{
    height: 0.6rem;
    background-color: #fff;
    text-align: center;
    padding: 0.18rem 0.3rem;   
}
#haixuan {
  height: 0.6rem;
  display: flex;
  justify-content: space-around;
  border: 1px solid #fe0000;
  font-size: 0.28rem;
  line-height: 0.6rem;
}
.haixuans{
 width: 33.33%;
}
.mousehover{
  color: #e03434;
}
.haixuan_bgc{
    color: white;
    background-color: #fe0000;
}
.haixuan_list {
  background-color:#edeef3;
  padding: 0.2rem;
  padding-bottom: 1.5rem;
}
.list {
 margin: 0 auto; 
  background-color:white;
   margin-bottom: 0.2rem;
}
.list_height{
    margin-bottom: 0.2rem;
    background-color:white;
}
.list_top {
   height: 0.7rem;
  line-height: 0.7rem;
  background-color: white;
   position: relative; 
   font-size: 0.2rem;
   padding: 0 0.2rem;
}
.list_t_l{
  text-align: center;
  display: flex;
  align-items: flex-end; 
}

.list_t_l img {
  width: 0.4rem;
  height: 0.24rem;
  margin-bottom: 0.23rem;
}
.toupiao {
  display: flex;
  align-items: center;
  padding-left: 10px;
  position: absolute;
  right: 0.2rem;
}
.toupiao img{
    width: 0.28rem;
    height: 0.28rem;
    margin-right: 0.05rem;
    margin-top: 0.17rem;
}
.list_center{   
    height: 1.6rem;
    display: flex;
    justify-content: flex-start;
    padding: 0 0.2rem;
}
.list_c_l_img{
    width: 1.4rem;
    height: 1.4rem;
    background: url("../../../../img/xingkong.png") no-repeat;
    background-size: 100% 100%;
}
.list_c_r{
    display: flex;
    width: 100%;
    height: 1.4rem;
    justify-content: flex-start;
    flex-wrap: wrap;
}
.list_c_r_name{
    width: 40%;
    height: 0.42rem;
    padding-left: 0.22rem;
    text-align: left;
    overflow: hidden;
    font-size: 0.24rem;
}
.list_c_b{
    height: 0.5rem;
    padding: 0 0.2rem;
    display: flex;
    justify-content: flex-start;
    position: relative;
}
.list_c_b_huo{
    width: 1.4rem;
    height: 0.28em;
   margin-top: 0.06rem;
}
.list_c_b_huo ul li{
    width: 0.28rem;
    height: 0.28rem;
    background: url("../../../../img/huo.png");
    background-size: 0.28rem 0.28rem;
    float: left;
}
.list_c_b_p{
    font-size: 0.24rem;
    color: #ccc;
    margin-left: 0.4rem;
}
.list_c_b_paiming{
   color: #fe0000;
   font-size: 0.24rem;
   position: absolute;
   right: 0.2rem;
   top: -0.05rem;
   display: flex;
   align-items: center;
}
.list_c_b_paiming img{
    width: 0.20rem;
    height: 0.12rem;
    margin-left: 0.1rem;
}
/****/
.project_name{
    width: 100%;
    height: 3.3rem;
}
.project_name_list{
    display: flex;
    width: 100%;
    height: 1.1rem;
    justify-content: flex-start;
    background-color: white;
    position: relative;
      align-items: center;
     border-top: 1px solid #c3c3c3;
}
.project_name_list_l{
    margin-left: 0.1rem;
    font-size: 0.2rem;
}
.project_name_lis_c{
  display: flex;
  flex-direction: column;
  font-size: 0.2rem;
  margin-left: 0.22rem;
}
.project_name_lis_c_top{
    display: flex;
    justify-content: left;
    align-items: center;
}
.project_name_lis_c_top span:nth-child(1){
    font-size: 0.26rem;
    margin-right: 0.2rem;
}
.project_name_lis_c_top span:nth-child(2){
    font-size: 0.2rem;
    color: #c5c5c5;
}
.project_name_lis_c_b{
    font-size: 0.16rem;
    display: flex;
    justify-content: flex-start;
}
.project_name_lis_c_b span{
    text-align: left;
    padding-right: 0.26rem;
}
.project_name_lis_r{
    width: 0.8rem;
    height: 0.4rem;
    line-height: 0.4rem;
    position: absolute;
    right: 0.16rem;
    top: 50%;
    transform: translateY(-50%);
    color: #fe0000;
   border: 1px solid #fe0000; 
   font-size: 0.24rem;  
}
.project_name_lis_r_at{
    color: #c3c3c3;
    border: 1px solid #c3c3c3;
}
</style>





